.profile{}
.user{
    display: grid;
    grid-template-areas: "ua un";
    grid-template-columns: 110px 10fr;
    margin: 0px 0px 30px 0px;
}
.avatar{
    grid-area: ua;
}
.avatar__image{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid #ccc;
}

.user__data{
    grid-area: un;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}
.user__name{
    margin: 0px 0px 0px 0px;
}
.user__status{
    color: #cccccc;
}
